import { useEffect, useState } from 'react';
import Services from '../../../../services/Services';
import { Modal } from '../../../elements/Modal';
import MatchForm from '../../MatchForm/MatchForm';
import MatchList from '../../MatchList/MatchList';
import './TeamCard.css';

function TeamCard({team}) {
  const [modalIsOpen, setIsOpen] = useState([false]);
  const [matches, setMatches] = useState([]);

  useEffect(() => {
    setMatches(team.matches);
  }, [team]);
 
  const closeModal = () => {
    setIsOpen([false]);
  };

  const onDeleteMatch = (e, match) => {
    e.stopPropagation();
    Services.tournaments().deleteMatch(team.tournamentId, team.id, match.id);
  };

  const deleteTeam = () => {
    Services.tournaments().deleteTeam(team.tournamentId, team.id);
  };

  const onSaveMatch = (match) => {
    setMatches([...matches, match]);
    closeModal();
  };

  const players = team.players.map((player, index) => <span className='mr text-sm' key={index}>{player}</span>)

  return (
    <div className='card v-layout align-stretch'>
      <div className='h-layout'>
        <h3 className='text-sm flex-grow'>{team.name ? team.name : team.id.substring(0, 4)}</h3>
        <button className='btn--secondary' onClick={() => deleteTeam()}><i className='fas fa-trash'></i></button>
      </div>
      <button className='btn--tertiary flex-grow' onClick={() => setIsOpen([true])}>Add Match</button>
      <h4 className='text-accent'>Players</h4>
      <div className='h-layout justify-stretch'>{players}</div>

      <h4 className='text-accent'>Matches</h4>
      <MatchList matches={matches} onDelete={(e, match) => onDeleteMatch(e, match)}/>
      
      <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          size='sm'
        >
        <MatchForm team={team} onSave={(match) => onSaveMatch(match)} />
      </Modal>
    </div>
  );
}

export default TeamCard;
