import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';
import { useEffect, useState } from 'react';
import './RandomTeams.css';
import Services from '../../../services/Services';

let teamNames = [];

function RandomTeams({tournament, onSave}) {
  const [randomTeams, setRandomTeams] = useState([{name: '', players: []}])

  useEffect(() => {
    setNewNames().then(() => {
      generate();
    });
  }, [])

  const setNewNames = async () => {
    teamNames = [];
    const teamCount = tournament.players.length / tournament.teamSize;
    for(let i = 0; i < teamCount; i++){
      const randomName = await Services.names().getName();
      teamNames.push(randomName === 'undefined undefined' ? uuidv4().substring(0, 4) : randomName);
    }
  }

  const generate = () => {
    const teamCount = tournament.players.length / tournament.teamSize;

    const playersShuffled =  _.shuffle(tournament.players);
    const randomTeams = []
    for(let i = 0; i < teamCount; i++) {
      const teamPlayers = [];
      for(let j = 0; j < tournament.teamSize; j++) {
        teamPlayers.push(playersShuffled[i*tournament.teamSize + j])
      }
      randomTeams.push({
        id: uuidv4(),
        name: teamNames[i],
        tournamentId: tournament.id,
        players: teamPlayers,
        matches: []
      })
    }
    
    setRandomTeams(randomTeams);
  }

  const saveTeams = (teams) => {
    Services.tournaments().saveTeams(tournament.id, teams);
  }

  const save = async () => {
    saveTeams(randomTeams);
    await setNewNames();
    onSave();
  }

  const teams = randomTeams.map((team, index) => {
    const players = team.players.map((player) => <span className='mr' key={player}>{player}</span>)
    return (
      <div className='mb-3' key={index}>
        <h3>{team.name}</h3>
        <div className='h-layout justify-left'>{players}</div>
      </div>
      
    )
  })

  return (
    <div>
      {teams}
      <div className='h-layout justify-stretch'>
        <button className='flex-grow' onClick={() => generate()} >Generate</button>
        <button className='ml' onClick={() => save()} >save</button>
      </div>
    </div>
  );
}

export default RandomTeams;
