// Initialize Firebase
firebase.initializeApp(firebaseConfig);

// Get a reference to the database service
var database = firebase.database();

export default class FirebaseService {
  
  detach() {
    database.ref('tournaments').off();
  }

  async getTournaments() {
    return database.ref('tournaments').once('value').then((snapshot) => {
      return JSON.parse(snapshot.val());
    });
  }
  
  getLiveTournaments(callback) {
    var tournamentsRef = firebase.database().ref('tournaments');
    tournamentsRef.on('value', (snapshot) => {
      callback(JSON.parse(snapshot.val()));
    });
  }
  
  async saveTournaments(tournaments) {
    return database.ref('tournaments').set(JSON.stringify(tournaments));;
  }

}